<!-- The VideoPlay component, including the welcome and logo -->
<template>
    <div>
        <h2 class="text-left video-title">{{ title }}</h2>
        <div class="row">
            <div class="col-md-9 col-sm-12">
                <div class="embed-responsive embed-responsive-16by9 mt-3">
                    <!-- After detecting the video ends, call OnEnd. The video will autoplay -->
                    <video class="embed-responsive-item" :src="link" @ended="onEnd" controls autoplay/>
                </div>
                <div class="border-bottom">
                    <div class="mt-3 mb-3 clearfix">
                        <div class="views float-left">
                            {{ views }} views
                        </div>
                        <div class="likes float-right">
                            <!-- Flag the two click by onModifyLike to simplify the logic implementation -->
                            <span class="mr-4">
                                <img @click="onModifyLike(1)"
                                     src="../static/svg/thumbs_up.svg" alt="thumb up image"> {{ likes }}
                            </span>
                            <img @click="onModifyLike(-1)"
                                 src="../static/svg/thumbs_down.svg" alt="thumb down image"> {{ dislikes }}
                        </div>
                    </div>
                </div>
            </div>

            <div class="col-md-3 offset-md-0 col-sm-6 offset-sm-3">
                <!-- Toggle the border highlight(as symbol of selection) by reading the isActive array -->
                <div v-for="(playItem, index) in playlist" :key="playItem.title" class="mt-3">
                    <img @click="changeCurrentPlay(index)" :class="{ 'border border-custom': isActive[index] }"
                         :src="playItem.thumb" :alt="'thumb for ' + playItem.title" class="img-fluid">
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    // Import the given json file
    import json from "../static/playlist";

    export default {
        name: "videoPlay",
        data: function () {
            return {
                videoInfo: [],  // an array of objects which keeps a record of all the video related info
                playlist: json, // the given json
                currentPlay: 0, // the current video that is on playing
                isActive: [],   // a one-hot array indicating (by setting to 1) which (by index) video is playing
                title: "",      // The following are the specific info about the current playing video
                link: "",
                views: 0,
                likeStatus: 0,  //likeStatus: 1 for like, -1 for unlike, 0 for neither
                likes: 0,
                dislikes: 0
            };
        },
        mounted() {
            // Use local storage to store video info
            if (localStorage.getItem('videoInfo')) {
                // Each time fetch the info from storage, if exits
                this.videoInfo = JSON.parse(localStorage.getItem('videoInfo'));
            } else {
                // The info isn't found, construct a new one and set to default values
                for (let vid of this.playlist) {
                    vid['views'] = 0;
                    vid['likeStatus'] = 0;
                    vid['likes'] = 0;
                    vid['dislikes'] = 0;
                    this.videoInfo.push(vid);
                }
                localStorage.setItem('videoInfo', JSON.stringify(this.videoInfo));
            }
            // Load the current info from the up-to-date videoInfo array to each of the status variable(e.g. title, likes)
            this.updateInfo();

            //Set the default of isActive, in a one-hot manner
            for (let i = 0; i < this.playlist.length; i++) {
                this.isActive.push(0);
            }
            this.isActive[0] = 1;
        },
        watch: {
            currentPlay: function () {
                this.updateInfo();
            }
        },
        methods: {
            changeCurrentPlay: function (index) {
                // Every time the current play changes, we also need to update the isActive
                this.isActive[this.currentPlay] = 0;
                this.currentPlay = index;
                this.isActive[index] = 1;
            },
            updateInfo: function () {
                const thisVideoInfo = this.videoInfo[this.currentPlay];
                this.title = thisVideoInfo.title;
                this.link = thisVideoInfo.video;
                this.views = thisVideoInfo.views;
                this.likeStatus = thisVideoInfo.likeStatus;
                this.likes = thisVideoInfo.likes;
                this.dislikes = thisVideoInfo.dislikes;
            },
            updateLocal: function () {
                // Writing into local storage require stringify the data
                const parsed = JSON.stringify(this.videoInfo);
                localStorage.setItem('videoInfo', parsed);
            },
            onEnd: function () {
                this.views += 1;
                this.videoInfo[this.currentPlay].views = this.views;
                this.updateLocal();
            },
            onModifyLike: function (likeType) {
                // The logic is not that trivial, we need to consider different cases of current like state
                if (likeType === 1) {
                    switch (this.likeStatus) {
                        case 0:
                            this.likes++;
                            this.likeStatus = 1;
                            break;
                        case -1:
                            this.likes++;
                            this.dislikes--;
                            this.likeStatus = 1;
                            break;
                        case 1:
                            this.likes--;
                            this.likeStatus = 0;
                            break;
                        default:
                            console.log('Error in likeType');
                    }
                }
                if (likeType === -1) {
                    switch (this.likeStatus) {
                        case 0:
                            this.dislikes++;
                            this.likeStatus = -1;
                            break;
                        case -1:
                            this.dislikes--;
                            this.likeStatus = 0;
                            break;
                        case 1:
                            this.likes--;
                            this.dislikes++;
                            this.likeStatus = -1;
                            break;
                        default:
                            console.log('Error in likeType');
                    }
                }
                this.videoInfo[this.currentPlay].likeStatus = this.likeStatus;
                this.videoInfo[this.currentPlay].likes = this.likes;
                this.videoInfo[this.currentPlay].dislikes = this.dislikes;
                this.updateLocal();
            }
        }
    }
</script>
